<template>
    <div class="opinionReports staticReports">
    <el-container>
      <el-header>
        <div class="loadingtext staticReports minWidth"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
          <div class="taticReports-content">
            <el-row type="flex" class="staticReports-header" justify="space-between">
              <el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="icon"></i>
              </el-col>
              <el-col :span="6" class="titleBtn clearfix">
                <el-button round type="primary"  @click="refresh" size="mini">重置</el-button>
                <el-button round type="primary"  size="mini" @click="inquire">搜索</el-button>
              </el-col>
            </el-row>
            <br>
            <!--<hr>-->
            <div class="statement reportCondition">
              <span class="fz16">动态-动态-岗位/人-审批意见的统计</span>
            <el-row class="staticReports-middle lh35 staticReportsMiddle">

            <!--选人控件input begin-->
            <el-col :sm="4" :md="1"  class="processSelect"><span>账号选择</span></el-col>
              <el-col :span="7" class="classSelectMR  process-inputbox">
              <i class="iconfont icon-member" @click="openDialog('originator_dailog')" ></i>
                <el-select
                  v-model="model.userName[0]"
                  filterable
                  remote
                  reserve-keyword
                  clearable
                  popper-class="orgstates"
                  :remote-method="querySearchUser"
                  value-key='userId'
                  placeholder=""
                  @change = "personChange"
                  @visible-change="visibleChange"
                  @remove-tag = "removeUser"
                  @clear="emptyselect"
                  :loading="loading"
                  ref="userId">
                  <el-option
                    v-for="(item,index) in orgStates"
                    :key="index"
                    :label="item.value"
                    :value="item">
                    <div class="userheight" style="font-weight: 600" >{{item.value}}({{item.userId}})</div>
                    <div class="fullpathsize">{{item.orgFullPath}}</div>
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="5" ><i  :class="[hintwringShow?'el-icon-warning':'']"></i><span>{{hintwring}} </span></el-col>
              <el-col :span="2"> <el-checkbox v-model="fa" @change="isChecked" >过滤掉为“空”的意见</el-checkbox></el-col>
            </el-row>
            <!--选人控件input end-->

            <el-row>
              <el-col :span="4" class="buttonWrap buttonWrapTA" >
                <!--<el-button type="primary"  size="mini" @click="inquire"><i class="iconfont el-icon-search " ></i>查询</el-button>
                <el-button type="primary"  @click="refresh" size="mini"><i class="iconfont el-icon-refresh" ></i>重置</el-button>-->
                <el-button round  class="upload" type="primary" @click="exportList()" icon="iconfont icon-export" size="mini">导出
                </el-button>
              </el-col>
            </el-row>

          </div>

          <div class="statement">
                <el-table
                  ref="singleTable"
                  :data="tableData"
                  highlight-current-row
                  border
                  :height="showList?fullHeight-405:fullHeight-280"
                  @sort-change="sortChange">

                  <el-table-column
                    property="procInstNO"
                    label="流程实例编号" width="150" sortable='custom'>
                  </el-table-column>
                  <el-table-column
                    property="processName"
                    label="流程名称"  align="left" sortable='custom'>

                  </el-table-column>
                  <el-table-column
                    property="topic"
                    width="125"
                    label="流程主题" sortable='custom'>
                  </el-table-column>
                  <el-table-column
                    property="activityName"
                    label="环节名称" width="150" sortable='custom'>

                  </el-table-column>
                  <el-table-column
                    property="approval"
                    label="审批结果" width="100" sortable='custom'>
                  </el-table-column>
                  <el-table-column
                    property="comment"
                    label="审批意见" width="120" sortable='custom' >
                    <template slot-scope="scope">
                      <div v-html="scope.row.comment"></div>

                    </template>
                  </el-table-column>
                  <el-table-column
                    property="approvalDate"
                    label="审批时间" width="150" sortable='custom'>
                    <template slot-scope="scope">{{scope.row.approvalDate | moment("YYYY-MM-DD")}}</template>
                  </el-table-column>
                  <div slot="empty" v-show="!imgLoading">
                    <p :style="{'marginTop': '23px'}">暂无数据</p>
                  </div>
                  <div slot="append">
                    <!--分页-->
                    <page :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                        @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                </div>
                </el-table>
            </div >
          </div>
        </div>
        <!--弹窗组件-->

        <component :is="comp.component"
                   @hideComponent="hideComponent"
                   v-if="showdialog"
                   @sureSubmit="showMsgfromchild"
                   v-bind:oneCheck="oneCheck"
                   v-bind:childDataUrl="childDataUrl"
                   v-bind:queryName="queryName">
        </component>

      </el-header>
    </el-container>
  </div>
</template>
<script>
  import originator_dailog from '../components/dailog/originator_dailog.vue'
  import util from '../js/util'
  import loading from '../plugins/loading/loading.js'
  import page from "../plugins/page/index"

  export default {
    name: 'opinionReports',
    data() {
      return {
        isShow:true,
        showdialog: false,
        showList: false,//列表状态
        fullHeight: document.documentElement.clientHeight,
        arrt:'收起',
        icon:'el-icon-arrow-down',
        /*
        * 表格数据*/
        total:0,
        //动态弹出组件
        comp: {},
        hintwring:'',//提示信息
        hintwringShow:false,//提示信息是否出现
        pageIndex: 1,
        orgStates: [],
        total: 0,
        queryName:'',
        multiplelimit:1,//限制选人数量
        loading:false,
        pageSize: 10,
        pageSizesList: [10, 20, 50, 100],
        childDataUrl: {
          url: '',
          selectedPerson:[],
          chooseProject: {}
        },
        //传给子组件的值
        model:{
          selectForm: {
            userID:'',//账号选择
          },
          userName:[],
        },
        filterEmptyComment:0,//是否过滤意见为空
        orderBy:'',
        fa:false,

        exportTotal:[],
        oneCheck:true,
        tableData: [

        ],
        // 加载动画
        imgLoading: false
      }
    },
    methods:{
      // 输入框清空
      emptyselect(){
          this.childDataUrl.selectedPerson=[];
          this.model.userName=[]
      },
      /**
       *
       * 获取所有人员
       *
       */
      getUsers(userName) {
        let that = this;
        that.orgStates=[];
        that.$axios.post('/api/getUser',{'name': userName, 'deptId': '','isWork':'1'}).then((result) => {
          for (var i = 0; i < result.data.result.length; i++) {
            that.orgStates.push({
              'value':result.data.result[i].displayName,
              'userId':result.data.result[i].adAccount,
              'orgFullPath': result.data.result[i].orgNameFullPath,
              'vankeId':result.data.result[i].vankeId//添加新增vankId
            });
          }
          this.loading = false;
        }).catch(err => {
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        });

      },
      /**
       * 发起人模糊搜索
       */
      querySearchUser(userName) {
        this.queryName = userName;
         this.orgStates=[];
        if(this.model.userName.length==0){
          this.hintwring ='';
          this.hintwringShow = false;
          var that = this;
          this.loading = true;
          if (this.isSeach) {
            return;
          }
          this.isSeach = true;
          setTimeout(() => {
            that.isSeach = false;
            that.validationInput(that.queryName);
          }, 2000);
        }else if( userName.trim() ==''){
          this.hintwringShow = false;
          this.hintwring ='';
        }else{
          this.hintwringShow = true;
          this.hintwring = '注意：此处账号选择只能单选！';
        }

      },
      // 发起人选择改变
      personChange (data) {
        this.$set(this.$refs.userId,'query','');//选中后清空输入的文本
        this.childDataUrl.selectedPerson[0] = data
      },
      //删除用户
      removeUser(data){
        if (data.userId) {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data.userId == this.childDataUrl.selectedPerson[it].userId) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        } else {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data == this.childDataUrl.selectedPerson[it].value) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        }
      },
      //验证延时后的值是否跟当前文本框值相同
      validationInput(userName) {
        if(userName=='' || userName.length==0){
          this.loading = false;
          return null;
        }
        if(userName == this.$refs.userId.query){
          this.getUsers(userName);
        }else{
          this.orgStates=[];
        }
      },
      // 下拉隐藏时候隐藏验证提示
      visibleChange(val){
        this.orgStates = [];
        if(!val){
          this.hintwringShow = false;
          this.hintwring ='';
        }
      },
      //翻页
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex =1;
        this.pageSize = parseInt(pageSize);
        localStorage.myInitPageSize = val;
        this.select();
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.select();
      },
      //表格排序
      sortChange(column){

        if(this.model.userName.length !==0){
         this.orderBy = `${column.prop} ${column.order.replace('ending','')}`;
        this.select();
        }
      },
      //意见过滤
      isChecked(val){
        if(val ==true){
          this.filterEmptyComment = 1;
        }else {
          this.filterEmptyComment = 0;
        }
      },
     /*
       导出
      */
     exportList() {
      var that = this;
       if(that.childDataUrl.selectedPerson.length==0){
          that.$message.warning("请选择账号！做导出数据条件！");
          return;
       }
       that.model.selectForm.orderBy = that.orderBy;
       that.model.selectForm.filterEmptyComment = that.filterEmptyComment;

       // 判断是否存在localStorage
       if(localStorage.todoPageSize) {
         that.pageSize = Number(localStorage.todoPageSize)
       }
       let userId = '';
       that.model.selectForm.userID='';
       for (let origina of that.childDataUrl.selectedPerson) {
         userId += `VANKE\\${origina.userId};`;
       }
       //选择发起人
       if (userId) {
         that.model.selectForm.userID = userId.substring(0, userId.length - 1);
       }
       loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
       that.$axios.post('/api/exportExcelForTable',  {'tableName':'动态-动态-岗位/人-审批意见的统计','params':that.model.selectForm}, {responseType: 'blob'}).then((result) => {
         const content = result.data;
         const dowLoadFileName = '动态-动态-岗位/人-审批意见的统计';
         util.exportSearchList(dowLoadFileName, content);
         loading.endLoading();
       }).catch((err) => {
         loading.endLoading();
         that.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
       });
      },
      /*
      * 查询审批意见数据
      * */
      select(){

        this.model.selectForm.orderBy = this.orderBy;
        this.model.selectForm.filterEmptyComment = this.filterEmptyComment;
        // 判断是否存在localStorage
        if(localStorage.todoPageSize) {
          this.pageSize = Number(localStorage.todoPageSize)
        }
        let userId = '';
        this.model.selectForm.userID='';
        for (let origina of this.childDataUrl.selectedPerson) {
          userId += `VANKE\\${origina.userId};`;
        }
        //选择发起人
        if (userId) {
          this.model.selectForm.userID = userId.substring(0, userId.length - 1);
        }
        // loading.startLoading();
        this.imgLoading = true;
        this.model.selectForm.pageIndex = this.pageIndex-1;
        this.model.selectForm.pageSize = this.pageSize
        this.$axios.post('/api/ApprovalOpinions/page',this.model.selectForm).then(result=>{
          this.tableData =result.data.approvalOpinionsList;
          this.total = result.data.total
          // loading.endLoading();
          this.imgLoading = false;

        }).catch(err=>{
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
          // loading.endLoading();
          this.imgLoading = false;
        })
        this.model.selectForm={}
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */


      showMsgfromchild(data) {
        if (data.type && data.type == "project") {
          //选择组织架构
          // let txt = (data.item.orgName == undefined) ? data.last.lastNode : data.last.lastNode + "-" + data.item.orgName;
          if (this.oneCheck) {
            let txt = data.last.lastNode;
            this.model.companys_t = txt;
            this.model.companys = txt;
            if(this.model.companys.length > 15){
              this.model.companys = this.model.companys.substr(0,20)+'...';
            }
            this.model.selectForm.departmentID = data.item.orgID;
            this.model.selectForm.companyCode = data.last.lastId.toString();
          } else {
            var chooseDatas = data.items;
          }
        } else if (data.type && data.type == "process") {
          //选择流程类别
          this.processFormName = data.text;
          this.model.selectForm.processID = data.processID;
        } else if (data.type && data.type == "originator") { // 选择发起人
          this.childDataUrl.selectedPerson = [];
          this.model.userName = [];
          for (let origina of data.peoArr) {
            var userId =origina.userId;
            var vankeId = origina.vankeId
            var bool = false;
            for (var j = 0; j < this.childDataUrl.selectedPerson.length; j++) {
              if (this.childDataUrl.selectedPerson[j].userId === userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.childDataUrl.selectedPerson.push({userId:userId,value:origina.name,vankeId:vankeId});
              this.model.userName.push(origina.name);
            }
          }
        }
        this.hideComponent();
      },

      /*
      * 查询
      * */
      inquire(){
          this.pageIndex= 1;
          if(this.model.userName.length == 0){
            this.$message.warning("请选择账号!");
          }else {
            this.select()
      }
      },
      /*
 * 重置数据
 * */

      refresh() {
        this.orderBy = '';
        this.filterEmptyComment =0;
        this.fa=false;
        //所属部门
        this.$nextTick(() => {
          this.model.selectForm.userID ='';
          this.model.userName = [];//发起人
          this.childDataUrl.selectedPerson=[];//清空回填
        });
      },
      showToggle(){
        this.showList =! this.showList
        this.isShow = !this.isShow;
        if(this.isShow){
          this.arrt="收起"
          this.icon="el-icon-arrow-up"
        }else {
          this.arrt="展开"
          this.icon="el-icon-arrow-down"
        }
      },
      //弹窗 子组件 调用触发事件
      sureSubmit(data) {
      },


      /**
       * 弹窗控件
       */
      openDialog(type, url) {
        // 弹出框类型
        this.comp.component = type;
        this.showdialog = true;         util.showDailog_mask();//解决ie浏览器问题显示遮罩
        if (type == 'originator_dailog') {//发起人
          this.oneCheck = true;
          this.childDataUrl.getTree = 'getTree';
          this.childDataUrl.companytree = 'companytree/page';
        } else if (type == 'project_dialog') {//组织
          this.childDataUrl.getTree = 'getTree';
          this.childDataUrl.companytree = 'companytree/page';
        }
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;         util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
    },
    mounted(){

    },
    watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    },
    components:{
      'page': page,
      'originator_dailog':originator_dailog
    }
  }
</script>
<style lang="scss" scoped>
  // 发起人弹窗select文本框输入 begin
  .el-select-dropdown__item{
    font-family: "微软雅黑";
    height: 50px !important;
    font-weight: 100;
    padding: 4px 20px;
    line-height: 22px;
    color: #606266;
    font-size: 12px;
    .orgstates.el-select-dropdown.el-popper.is-multiple{

      .fullpathsize,
      .userheight{
        height: 20px !important;
      }
      .userheight{
        font-weight: 600;
      }
    }

    .el-select-dropdown__item.selected{
      font-weight: 100;
      background-color: #f5f7fa;
      .fullpathsize,
      .userheight{
        color: #4ca8fc!important;
      }
    }
  }

  // 发起人弹窗select文本框输入 end
</style>




